import anjay from '../../style.module.css'
import Thumb from '../../../../aset/thumbnail/kimetsu no yaiba/1.jpg'


function Eps1() {
  return (
    <a href="/kimetsueps1" className='text-dark w-10'>
      <div className={anjay.card}>
        <div class="row g-0">
          <div class="col-md-6">
            <img src={Thumb} class='img-fluid w-100' alt='thumb'></img>
          </div>
          <div className="col-md-6 p-2">
            <h5 class={anjay["card-title"]}>Episode 1</h5>
            <p class="card-text"><small class="text-muted">2019</small></p>
          </div>
        </div>
      </div>
    </a>
  );
}

export default Eps1;