import anjay from '../../aset/thumbnail/Kuzu.jpg';

function Kuzukiri() {
    return (
        <div className="card shadow-sm" id='card-kiri'>
          <div className="gambar">
            <img src={anjay} id='img-maincard' className="card-img-top" alt='thumb'></img>
          </div>
          <div className="card-body text-start">
            <h4>Kuzu no Honkai</h4>
            <p className="card-text">Winter 2017</p>
          </div>
        </div>
    );
}

export default Kuzukiri;
