import Bleachthumb from '../../aset/thumbnail/fmab.jpg'

function Fmabkiri() {
    return (
        <div className="card shadow-sm" id='card-kiri'>
          <div className="gambar">
            <img src={Bleachthumb} id='img-maincard' className="card-img-top" alt='thumb'></img>
          </div>
          <div className="card-body text-start">
            <h4>Fullmetal Alchemist: Brotherhood</h4>
            <p className="card-text">Spring 2009</p>
          </div>
        </div>
    );
}

export default Fmabkiri;
