import Thumb from '../../aset/thumbnail/spy x.jpg'

function Spykiri() {
    return (
        <div className="card shadow-sm" id='card-kiri'>
          <div className="gambar">
            <img src={Thumb} id='img-maincard' className="card-img-top" alt='thumb'></img>
          </div>
          <div className="card-body text-start">
            <h4>Spy x Family Part 2</h4>
            <p className="card-text">Fall 2022</p>
          </div>
        </div>
    );
}

export default Spykiri;
