import anjay from './episode.module.css';
import React, { useState } from 'react';

function App() {
  const [text, setText] = useState(<h6 id={anjay.synopsis}>Pada zaman Taisho, hiduplah Kamado Tanjiro, seorang anak lelaki yang berhati lembut dengan keluarganya di gunung. Suatu hari, sepulang menjual arang di kota, dia melihat jenazah keluarganya di rumah dibantai dengan keji oleh iblis.</h6>);
  const [activeButton, setActiveButton] = useState('button1');

  function handleClick(button) {
    if (button === 'button1') {
      setActiveButton('button1');
      setText(<h6 id={anjay.synopsis}>Pada zaman Taisho, hiduplah Kamado Tanjiro, seorang anak lelaki yang berhati lembut dengan keluarganya di gunung. Suatu hari, sepulang menjual arang di kota, dia melihat jenazah keluarganya di rumah dibantai dengan keji oleh iblis.</h6>);
    } else if (button === 'button2') {
      setActiveButton('button2');
      setText(
        <div id={anjay.genre}>
          <h6><b>Genre</b> : Anime, action, adventure.</h6>
          <h6><b>Pemain</b> :  Natsuki Hanae, Akari Kitō, Yoshitsugu Matsuoka</h6>
          <h6><b>Episode</b> : 26</h6>
          <h6><b>Season</b> : 1</h6>
        </div>
        );
    }
  }

  return (
    <div>
      <button onClick={() => handleClick('button1')} className={activeButton === 'button1' ? 'active' : ''} id={anjay.button1}>
        Synopsis
      </button>
      <button onClick={() => handleClick('button2')} className={activeButton === 'button2' ? 'active' : ''} id={anjay.button2}>
        Info
      </button>
      <p>{text}</p>

      <style>
        {`
          .active {
            background-color: #d28484;
            color: #fff;
          }

        `}
      </style>
    </div>
  );
}

export default App;
