import { Swiper, SwiperSlide} from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/autoplay";

// import required modules
import { Autoplay, Pagination } from "swiper";
import Onekiri from "./card-kiri/Onepiece";
import Mushoukokiri from "./card-kiri/Mushouko";
import Stainskiri from "./card-kiri/Stains";
import Fruitkiri from "./card-kiri/Fruit";
import Kaguyakiri from "./card-kiri/Kaguya";
import Fmabkiri from "./card-kiri/Fullmetal";
import Kuzukiri from "./card-kiri/Kuzu";
import Hxhkiri from "./card-kiri/Hxh";

export default function App() {
  return (
    <>
      <Swiper
        slidesPerView={5}
        spaceBetween={5}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination, Autoplay]}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        className="mySwiper"
      >
        <SwiperSlide><Onekiri /></SwiperSlide>
        <SwiperSlide><Hxhkiri /></SwiperSlide>
        <SwiperSlide><Mushoukokiri /></SwiperSlide>
        <SwiperSlide><Stainskiri /></SwiperSlide>
        <SwiperSlide><Fruitkiri /></SwiperSlide>
        <SwiperSlide><Kaguyakiri /></SwiperSlide>
        <SwiperSlide><Fmabkiri /></SwiperSlide>
        <SwiperSlide><Kuzukiri /></SwiperSlide>
      </Swiper>
    </>
  );
}
