import anime from '../style.module.css';
import Thumbnail from '../../../aset/thumbnail/kny.jpg';
import Icon from '../../../aset/icon.png';
import Button from 'react-bootstrap/Button';

// navbar
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import LOGO from "../../../aset/LOGO.png";

// eps
import Episode1 from './eps-card/Eps1';
import Episode2 from './eps-card/Eps2';
import Episode3 from './eps-card/Eps3';
import Episode4 from './eps-card/Eps4';
import Episode5 from './eps-card/Eps5';
import Episode6 from './eps-card/Eps6';
import Episode7 from './eps-card/Eps7';
import Episode8 from './eps-card/Eps8';
import Episode9 from './eps-card/Eps9';
import Episode10 from './eps-card/Eps10';
import Episode11 from './eps-card/Eps11';
import Episode12 from './eps-card/Eps12';
import Episode13 from './eps-card/Eps13';
import Episode14 from './eps-card/Eps14';
import Episode15 from './eps-card/Eps15';
import Episode16 from './eps-card/Eps16';
import Episode17 from './eps-card/Eps17';
import Episode18 from './eps-card/Eps18';
import Episode19 from './eps-card/Eps19';
import Episode20 from './eps-card/Eps20';
import Episode21 from './eps-card/Eps21';
import Episode22 from './eps-card/Eps22';
import Episode23 from './eps-card/Eps23';
import Episode24 from './eps-card/Eps24';
import Episode25 from './eps-card/Eps25';
import Episode26 from './eps-card/Eps26';



function Kimetsu() {
  return (
    <div className="induk">
      <Navbar expand="lg" id='navbar' variant="dark">
        <Container id='nav-container'>
          <Navbar.Brand href="#"><img src={LOGO} id='logo' alt='logo'></img></Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="ms-auto my-2 my-lg-0 mt-0"
              style={{ maxHeight: '95px' }}
              navbarScroll
            >
              <Nav.Link className='pt-1' href="/">Home</Nav.Link>
              <Nav.Link className='pt-1' href="/list">List</Nav.Link>
              <Nav.Link className='pt-1' href="#action2">Genre</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div id={anime['flex-container']} className="shadow">
        <div id={anime.kiri}>
          <div className="content text-center">
            <div className={anime.gambar}>
              <img id={anime.thumbnail} src={Thumbnail} alt='thumbnail'></img>
            </div>
            <div className={anime['kiri-body']}>
              <h4 className='mt-1'>kimetsu no yaiba</h4>
              <div className="container d-flex justify-content-center">
                <p className={anime.ratting}>13+</p><p>Anime, action, adventure</p>
              </div>
              <a href="/kimetsueps1"><button id={anime.mulai} className='mt-3'><img src={Icon} alt='icon' id={anime.icon}></img> Mulai Menonton</button></a>
              <p className={anime.synopsis}>Pada zaman Taisho, hiduplah Kamado Tanjiro, seorang anak lelaki yang berhati lembut dengan keluarganya di gunung. Suatu hari, sepulang menjual arang di kota, dia melihat jenazah keluarganya di rumah dibantai dengan keji oleh iblis.</p>
              <div className="text-start mt-4">
                <Button className={anime['btn-share']}>Share</Button>
                <a href="https://myanimelist.net/anime/38000/Kimetsu_no_Yaiba"><Button className={anime['btn-share']}>MAL</Button></a>
              </div>
              <div className="info text-start mt-2">
                <p><b>Pemain :</b> Natsuki Hanae, Akari Kitō, Yoshitsugu Matsuoka</p>
                <p><b>Sutradara :</b> Haruo Sotozaki</p>
                <p><b>Episode :</b> 26 <b>Season 1</b></p>
              </div>
            </div>
          </div>
        </div>
        <div id={anime.kanan}>
          <Episode1 />
          <Episode2 />
          <Episode3 />
          <Episode4 />
          <Episode5 />
          <Episode6 />
          <Episode7 />
          <Episode8 />
          <Episode9 />
          <Episode10 />
          <Episode11 />
          <Episode12 />
          <Episode13 />
          <Episode14 />
          <Episode15 />
          <Episode16 />
          <Episode17 />
          <Episode18 />
          <Episode19 />
          <Episode20 />
          <Episode21 />
          <Episode22 />
          <Episode23 />
          <Episode24 />
          <Episode25 />
          <Episode26 />
        </div>
      </div>
    </div>

  );
}

export default Kimetsu;