import Bleachthumb from '../../aset/thumbnail/stains.jpg'

function Mushoukokiri() {
    return (
        <div className="card shadow-sm" id='card-kiri'>
          <div className="gambar">
            <img src={Bleachthumb} id='img-maincard' className="card-img-top" alt='thumb'></img>
          </div>
          <div className="card-body text-start">
            <h4>Steins;Gate</h4>
            <p className="card-text">Spring 2011</p>
          </div>
        </div>
    );
}

export default Mushoukokiri;
