import Carousel from 'react-bootstrap/Carousel';
import img1 from '../aset/bleach.jpg';
import img2 from '../aset/chainsaw.jpg';
import img3 from '../aset/bluelock.jpg';
import '../App.css';
import { faPlay } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function HomeCarousel() {
  return (
    <Carousel id='main-carousel'>
      <Carousel.Item>
        <img src={img1} alt='slide1'></img>
        <Carousel.Caption className='text-end'>
          <button><FontAwesomeIcon icon={faPlay} /> Watch</button>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img src={img2} alt='slide2'></img>
        <Carousel.Caption className='text-end'>
          <button><FontAwesomeIcon icon={faPlay} /> Watch</button>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img src={img3} alt='slide3'></img>
        <Carousel.Caption className='text-end'>
          <button><FontAwesomeIcon icon={faPlay} /> Watch</button>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
  );
}

export default HomeCarousel;