import Eps from '../episode/episode.module.css'

// navbar
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import LOGO from "../../../../aset/LOGO.png";
import Eps1 from '../eps-card/Eps1';
import Eps3 from '../eps-card/Eps3';
import Eps4 from '../eps-card/Eps4';
import Eps5 from '../eps-card/Eps5';
import Eps6 from '../eps-card/Eps6';
import Eps7 from '../eps-card/Eps7';
import Eps8 from '../eps-card/Eps8';
// navbar end

import Tombol from './tombol-kimetsu';


function Kimetsu() {
  return (
    <div className="induk">
      <Navbar expand="lg" id='navbar' variant="dark">
        <Container id='nav-container'>
          <Navbar.Brand href="#"><img src={LOGO} id='logo' alt='logo'></img></Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="ms-auto my-2 my-lg-0 mt-0"
              style={{ maxHeight: '95px' }}
              navbarScroll
            >
              <Nav.Link className='pt-1' href="/">Home</Nav.Link>
              <Nav.Link className='pt-1' href="/list">List</Nav.Link>
              <Nav.Link className='pt-1' href="#action2">Genre</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className="main-content">
        <iframe SRC="https://sbbrisk.com/e/jgx6y4i0xwzr.html" FRAMEBORDER="0" MARGINWIDTH="0" MARGINHEIGHT="0" SCROLLING="NO" WIDTH="100%"HEIGHT="605" title="episode1" allowfullscreen></iframe>
        <div className={Eps.content}>
          <div className={Eps.kiri}>
            <div id={Eps.tombol} className="text-center">
              <a href='/kimetsueps1'><button>Reverse</button></a>
              <a href='/kimetsu'><button>All</button></a>
              <button>Next</button>
            </div>
            <div className={Eps['kiri-content']}>
              <h3>Kimetsu no Yaiba Episode 2</h3>
              <h5 className='mb-3'>2019</h5>
              <Tombol/>
            </div>
          </div>
          <div className={Eps.kanan}>
            <h3>Episode</h3>
            <div className={Eps.scroll}>
              <div className={Eps.episode}>
                <Eps1 />
                <Eps3 />
                <Eps4 />
                <Eps5 />
                <Eps6 />
                <Eps7 />
                <Eps8 />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  );
}

export default Kimetsu;