import Fruit from '../../aset/thumbnail/fruit.jpg'

function Fruitkiri() {
    return (
        <div className="card shadow-sm" id='card-kiri'>
          <div className="gambar">
            <img src={Fruit} id='img-maincard' className="card-img-top" alt='thumb'></img>
          </div>
          <div className="card-body text-start">
            <h4>Fruits Basket</h4>
            <p className="card-text">Summer 2001</p>
          </div>
        </div>
    );
}

export default Fruitkiri;
