import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';

// navbar
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import LOGO from "../aset/LOGO.png";
// nav end

import Kimetsu from '../aset/thumbnail/kny.jpg';
import Gingga from '../aset/thumbnail/gingga.jpg';
import Gatsu from '../aset/thumbnail/3-gatsu no Lion.jpg'

// img main card
// import Bleach from '../component/card-kiri/Bleach';
// import Boku from '../component/card-kiri/Boku';
// import Mob from '../component/card-kiri/Mob';
// import Blue from '../component/card-kiri/Blue';
// import Chain from '../component/card-kiri/Chain';
// import Spy from '../component/card-kiri/spy';
// import Kaguya from '../component/card-kiri/Kaguya';
// import Snk from '../component/card-kiri/Snk end';
// img main card end

// card end

import Imgiklan1 from '../aset/yorushika tshirt.png';
import Imgiklan2 from '../aset/nirvana.png';
import Sliderongoing from '../component/Slider-ongoing';
import Slidercomplete from '../component/Slider-complete'
import Carousel from '../component/Carousel';
<script src="https://kit.fontawesome.com/yourcode.js" crossorigin="anonymous"></script>

function Home() {
  return (
    <div className="induk">
      <Navbar expand="lg" id='navbar' variant="dark">
        <Container id='nav-container'>
          <Navbar.Brand href="/"><img src={LOGO} id='logo' alt='logo'></img></Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="ms-auto my-2 my-lg-0"
              style={{ maxHeight: '115px' }}
              navbarScroll
            >
              <Nav.Link id='menu' href="/" active>Home</Nav.Link>
              <Nav.Link id='menu' href="/list">List</Nav.Link>
              <Nav.Link id='menu' href="/genre">Genre</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <div className='container' id='container-carousel'><Carousel /></div>
      <Container fluid className='text-center w-100' id='container'>
        <div className='row' id='main'>
          <div id='kiri'>
      {/* ongoing */} 
            <div className='ongoing-anime'>
              <Navbar variant="dark" className='w-100 p-0' id='head-complete'>
                <Container>
                  <Navbar.Brand href="#home">Ongoing</Navbar.Brand>
                  <Nav className="ms-auto">
                    <Button className='more'>more</Button>
                  </Nav>
                </Container>
              </Navbar>
              <Sliderongoing />
            </div>
            <div className='complete-anime mt-2'>
              <Navbar variant="dark" className='w-100 p-0' id='head-complete'>
                <Container>
                  <Navbar.Brand href="#home">Complete</Navbar.Brand>
                  <Nav className="ms-auto">
                    <Button className='more'>more</Button>
                  </Nav>
                </Container>
              </Navbar>
              <Slidercomplete />
            </div>
          </div>
      {/* ongoing end */}
          <div id='kanan'>
            <Navbar variant="dark" className='w-95 p-0' id='rekomendasi'>
              <Container>
                <Navbar.Brand href="#home" className='mx-auto'>rekomendasi</Navbar.Brand>
              </Container>
            </Navbar>
            <div  id='card-kanan1'>
              <div className="row g-0">
                <div className="col-md-4" id='gambar'>
                  <img className="img-fluid" alt='img-yorushika' src={Imgiklan1}></img>
                </div>
                <div className="col-md-8 align-self-center" id='body-iklan'>
                  <div className="card-body ">
                    <h4 className="card-title" id='card-kanan1-title'>yorushika t-shirt</h4>
                    <h5 id='harga'>Rp 190.000</h5>
                    <button className='btn-iklan btn1'>checkout</button>
                  </div>
                </div>
              </div>
            </div>
            <div id='card-kanan2'>
              <div className="row g-0">
                <div className="col-md-4" id='gambar'>
                  <img className="img-fluid" alt='img-nirvana' src={Imgiklan2}></img>
                </div>
                <div className="col-md-8 align-self-center" id='body-iklan'>
                  <div className="card-body ">
                    <h4 className="card-title" id='card-kanan2-title'>yorushika t-shirt</h4>
                    <h5 id='harga'>Rp 190.000</h5>
                    <button className='btn-iklan btn2'>checkout</button>
                  </div>
                </div>
              </div>
            </div>
            <a href="/list">
              <div className="cardkanan">
                <div className="row g-0">
                  <div className="col-md-4 text-start" id='gambar'>
                    <img className="img-fluid" alt='imgreko' src={Gatsu}></img>
                  </div>
                  <div className="col-md-8" id='body-iklan'>
                    <div className="card-body text-start">
                      <h4 className="card-title">3-gatsu no Lion 2nd</h4>
                      <div id='artikel'>
                        <p><b>Genres:</b> Drama, Slice of Life</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </a>
            <a href='/kimetsu'>
              <div className="cardkanan">
                <div className="row g-0">
                  <div className="col-md-4 text-start" id='gambar'>
                    <img className="img-fluid" alt='imgreko' src={Kimetsu}></img>
                  </div>
                  <div className="col-md-8" id='body-iklan'>
                    <div className="card-body text-start">
                      <h4 className="card-title">Kimetsu no Yaiba</h4>
                      <div id='artikel'>
                        <p><b>Genres:</b> Action, Fantasy</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </a>
            <a href='/kimetsu'>
              <div className="cardkanan">
                <div className="row g-0">
                  <div className="col-md-4 text-start" id='gambar'>
                    <img className="img-fluid" alt='imgreko' src={Gingga}></img>
                  </div>
                  <div className="col-md-8" id='body-iklan'>
                    <div className="card-body text-start">
                      <h4 className="card-title">Ginga Eiyuu Densetsu</h4>
                      <div id='artikel'>
                        <p><b>Genres:</b> Drama, Sci-Fi</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </div>
        </div>
      </Container>
    </div>
  );
}

export default Home;