import Bleachthumb from '../../aset/thumbnail/bleach.jpg'

function Bleachkiri() {
    return (
        <div className="card shadow-sm" id='card-kiri'>
          <div className="gambar">
            <img src={Bleachthumb} id='img-maincard' className="card-img-top" alt='thumb'></img>
          </div>
          <div className="card-body text-start">
            <h4>Bleach: Sennen Kessen-hen</h4>
            <p className="card-text">Episode 9</p>
          </div>
        </div>
    );
}

export default Bleachkiri;
