import { Swiper, SwiperSlide} from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/autoplay";

// import required modules
import { Autoplay, Pagination } from "swiper";
import Bleachkiri from "./card-kiri/Bleach";
import Bluekiri from "./card-kiri/Blue";
import Bokukiri from "./card-kiri/Boku";
import Chainkiri from "./card-kiri/Chain";
import Snkkiri from "./card-kiri/Snk end";
import Spykiri from "./card-kiri/spy";
import Mobkiri from "./card-kiri/Mob";

export default function App() {
  return (
    <>
      <Swiper
        slidesPerView={5}
        spaceBetween={5}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination, Autoplay]}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        className="mySwiper"
      >
        <SwiperSlide><Bleachkiri /><h5 className="eps">Eps 5</h5></SwiperSlide>
        <SwiperSlide><Bluekiri /><h5 className="eps">Eps 2</h5></SwiperSlide>
        <SwiperSlide><Bokukiri /><h5 className="eps">Eps 4</h5></SwiperSlide>
        <SwiperSlide><Chainkiri /><h5 className="eps">Eps 9</h5></SwiperSlide>
        <SwiperSlide><Snkkiri /><h5 className="eps">Eps 2</h5></SwiperSlide>
        <SwiperSlide><Spykiri /><h5 className="eps">Eps 12</h5></SwiperSlide>
        <SwiperSlide><Mobkiri /><h5 className="eps">Eps 6</h5></SwiperSlide>
      </Swiper>
    </>
  );
}
